import { useMemo, useState } from 'react';
import MetricCard from '@/components/MetricCard';
import MetricChart from '@/components/MetricChart';

const formatTimestamp = (timestamp) => {
  try {
    return new Date(timestamp).toLocaleString();
  } catch {
    return timestamp;
  }
};

const getLatestValue = (metric) => metric.latest?.display || metric.latest?.raw || '—';

export default function ScorecardDashboard({ metrics, generatedAt }) {
  const orderedMetrics = useMemo(() => {
    const list = Array.isArray(metrics) ? metrics : [];
    return [...list].sort((a, b) => {
      if (a.category === b.category) {
        return a.name.localeCompare(b.name);
      }
      return a.category.localeCompare(b.category);
    });
  }, [metrics]);

  const [selectedMetricId, setSelectedMetricId] = useState(null);
  const selectedMetric =
    orderedMetrics.find((metric) => metric.id === selectedMetricId) || orderedMetrics[0] || null;
  const activeMetricId = selectedMetric?.id || '';
  const heroMetrics = orderedMetrics.slice(0, 6);
  const hasMetrics = orderedMetrics.length > 0;

  if (!hasMetrics) {
    return (
      <div className="error-panel">
        <h1>No metrics available</h1>
        <p>Verify that the spreadsheet contains the “Weekly EOS Scorecard” worksheet.</p>
      </div>
    );
  }

  return (
    <div className="dashboard">
      <header className="dashboard__header">
        <div>
          <p className="eyebrow">Weekly EOS Scorecard</p>
          <h1>IT Scorecard Overview</h1>
          <p className="muted">Rendering data pulled directly from the Excel workbook.</p>
        </div>
        <div className="timestamp">
          <p>Last refreshed</p>
          <strong>{formatTimestamp(generatedAt)}</strong>
        </div>
      </header>

      <section>
        <h2>Key Metrics</h2>
        <div className="metric-card-grid">
          {heroMetrics.map((metric) => (
            <MetricCard key={metric.id} metric={metric} />
          ))}
        </div>
      </section>

      <section className="metric-detail">
        <div className="metric-detail__header">
          <div>
            <h2>Metric Detail</h2>
            <p className="muted">Select a metric to visualize its weekly trend.</p>
          </div>
          <select value={activeMetricId} onChange={(event) => setSelectedMetricId(event.target.value)}>
            {orderedMetrics.map((metric) => (
              <option key={metric.id} value={metric.id}>
                {metric.category} — {metric.name}
              </option>
            ))}
          </select>
        </div>
        {selectedMetric ? (
          <>
            <MetricChart metric={selectedMetric} />
            <div className="metric-detail__table-wrapper">
              <table>
                <thead>
                  <tr>
                    <th>Week</th>
                    <th>Value</th>
                  </tr>
                </thead>
                <tbody>
                  {selectedMetric.series.map((point) => (
                    <tr key={`${selectedMetric.id}-${point.week}`}>
                      <td>{point.week}</td>
                      <td>{point.raw || '—'}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </>
        ) : (
          <p className="muted">No metric selected.</p>
        )}
      </section>

      <section>
        <h2>All Metrics</h2>
        <div className="metric-table-wrapper">
          <table>
            <thead>
              <tr>
                <th>Category</th>
                <th>Metric</th>
                <th>Goal</th>
                <th>Latest Week</th>
                <th>Latest Value</th>
                <th>Status</th>
              </tr>
            </thead>
            <tbody>
              {orderedMetrics.map((metric) => (
                <tr key={`table-${metric.id}`}>
                  <td>{metric.category}</td>
                  <td>{metric.name}</td>
                  <td>{metric.goal.goal || metric.goal.text || '—'}</td>
                  <td>{metric.latest.week}</td>
                  <td>{getLatestValue(metric)}</td>
                  <td className={`status-badge status-${metric.status || 'unknown'}`}>
                    {metric.status || 'unknown'}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </section>
    </div>
  );
}
